function content() {

    var ban = document.querySelector(".banner");
    var banUl = document.querySelector(".banner ul");
    var banLi = document.querySelectorAll(".banner ul li");

    var banImg = 0;     // 每张图片大小
    var mg = 0;          //banner位置 
    var startPoint = 0;   //初始化手指坐标
    var movePoint = 0;   //初始化手指滑动坐标
    var time             //定时器

    // 添加事件  触摸  移动  离开
    ban.addEventListener("touchstart", function feel(e) {
        var ev = e || window.e;
        var touch = ev.touches[0]; //获取第一个触点
        startPoint = Number(touch.pageX);  //页面触点X坐标
        mg = parseInt((banUl.currentStyle || getComputedStyle(banUl, null)).marginLeft);
        clearInterval(time)
    })
    ban.addEventListener("touchmove", function move(e) {
        var ev = e || window.e;
        var touch = ev.touches[length]; //获取所有触点
        movePoint = Number(touch.pageX)
        bannerMove()
    })
    ban.addEventListener("touchend", function end(e) {
        // 离开时判断向左还是向右
        if (movePoint - startPoint < -20 && mg <= 0 && mg >= -banImg * (banLi.length - 2)) {
            banUl.style.marginLeft = mg - banImg + "px";
        } else if (movePoint - startPoint > 20 && mg <= -banImg) {
            banUl.style.marginLeft = mg + banImg + "px";
        } else {
            banUl.style.marginLeft = mg + "px";
        }
        // 离开自动轮播

        time = setInterval(function () {
            mg = parseInt((banUl.currentStyle || getComputedStyle(banUl, null)).marginLeft);
            banImg = banLi[0].clientWidth;
            banUl.style.marginLeft = (mg - banImg) + "px";
            if (mg <= -banImg * (banLi.length - 1)) {
                banUl.style.marginLeft = "0px";
            } else {
                banUl.style.marginLeft = mg - banImg + "px";
            }
        }, 3000)


    })

    //banner跟随事件
    function bannerMove() {
        banImg = banLi[0].clientWidth;
        if (movePoint - startPoint + mg >= 0) {
            banUl.style.marginLeft = "0px";
        } else if (movePoint - startPoint + mg <= banImg * (banLi.length - 1) * -1) {
            banUl.style.marginLeft = banImg * (banLi.length - 1) * -1 + "px";
        } else {
            banUl.style.marginLeft = mg + (movePoint - startPoint) + "px";
        }
    }
    //banner 自动轮播
    window.onload = function bannerOwn() {
        time = setInterval(function () {
            mg = parseInt((banUl.currentStyle || getComputedStyle(banUl, null)).marginLeft);
            banImg = banLi[0].clientWidth;
            banUl.style.marginLeft = (mg - banImg) + "px";
            if (mg <= -banImg * (banLi.length - 1)) {
                banUl.style.marginLeft = "0px";
            } else {
                banUl.style.marginLeft = mg - banImg + "px";
            }
        }, 3000)
    }();
}

 export { content }


