<template>
<div class="market">
  <div class="header" :style="bg">
    <div class="head">
      <img :src="homeHead.src" alt>
    </div>
    <div class="search">
      <input type="text" name="" value="春季新款卫衣">
      <img src="../../assets/icon/search.png" alt>
    </div>
    <div class="service">
      <img src="../../assets/icon/kf.png" alt>
    </div>
  </div>
  <!-- banner -->
  <banner :banner="marketBan"></banner>
  <div class="mac">随心预约 · 就近选择</div>
  <!-- shop -->
  <shop :shop="marketShop"></shop>
  <shop :shop="marketShop"></shop>
  <shop :shop="marketShop"></shop>
  <foot :footImg="ftImg"></foot>
</div>
</template>

<script>
export default {
  data() {
    return{
      ftImg:{home:require("@/assets/footer/dp.png"),
             kind:require("../../assets/footer/md.png"),
             market:require("../../assets/footer/sp2.png"),
             car:require("@/assets/footer/gwc.png"),
             mine:require("@/assets/footer/grzx.png"),
             },
 
    marketBan:[{src:require("../../assets/banner/bannerc-1.jpg")},
                 {src:require("../../assets/banner/bannerc-2.jpg")},                
                 {src:require("../../assets/banner/bannerc-3.jpg")}                
                  ],  
    marketShop:{src:require("../../assets/shop/mendian1.jpg"),
                title:"大连时代广场",
                 content:"AKSBCBJWBU阿尔弗雷德 SF",
                  span:"DCSDCSD啊是大啊啊"},
              
		bg:"background:rgba(58, 58, 58,0);",
		homeHead:{src:require("../../assets/icon/dw.png")},
	}
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.scroll()
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.bg = "background:rgba(58, 58, 58," + 0.01 * scrollTop + ");";
    },
        scroll(){window.scrollTo(0,0);}

  }
};
</script>

<style scoped>
.market{
      margin-bottom: 1.1rem;
}
.mac{
  width: 100%;
    height: 0.9rem;
    line-height: 0.9rem;
    font-size: 0.36rem;
    color: #000000;
    text-align: center;
    font-weight: bolder;
}
.header{
  width:100%;
  height: 0.9rem;
  position:fixed;
  z-index: 999;
}
.header::after{
  content: "";
  clear: both;
  display: block;
}
.head{
    width: 0.67rem;
    height: 0.67rem;
    float: left;
    margin-left: 0.3rem;
    margin-top: 0.17rem;
    overflow: hidden;
}
.head>img{
  width: 65%;
}
.search{
    float: left;
    width: 5.3rem;
    height: 0.6rem;
    margin-top: 0.12rem;
    margin-left: 0.15rem;
    border-bottom: #FFFFFF solid 0.01rem;
}
.search::after{
  content: "";
  clear: both;
  display: block;
}
.search>input{
    background: none;
    outline: none;
    color: #fff;
    width: 4.5rem;
    border: none;
    height: 0.5rem;
    font-size: 0.28rem;
    margin-top: 0.05rem;
    float: left;
}
.search>img{
    float: left;
    display: block;
    width: 0.5rem;
    margin-left: 0.25rem;
    margin-top: 0.05rem;
}
.service{
    float: right;
    margin-right: 0.24rem;
    width: 0.5rem;
    margin-top: 0.2rem;
}
.service>img{width: 100%;}

</style>





