import Vue from 'vue'
import Router from 'vue-router'
import signIn from '@/components/signIn'
import home from  '@/components/page/home'
import kind from  '@/components/page/kind'
import car from  '@/components/page/car'
import market from  '@/components/page/market'
import mine from  '@/components/page/mine'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      name: 'signIn',
      component: signIn
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/market',
      name: 'market',
      component:market
    },
    {
      path: '/mine',
      name: 'mine',
      component:mine
    },
    {
      path: '/car',
      name: 'car',
      component:car
    },
    {
      path: '/kind',
      name: 'kind',
      component: kind
    }
  ]
})



