<template>
  <div class="sign-container">
    <form action="">
    <h3>welcome to shop</h3>
    <div class="sign-account">
       <img src="../assets/signIn/account.png">
       <input type="text" @blur="blurAccount" v-model="account"  placeholder="请输入账号">
    </div>
    <span>{{accountAlt}}</span>

    <div class="sign-password">
       <img src="../assets/signIn/password.png">
       <input type="password" v-model="password"  placeholder="请输入密码">
    </div>
    <div class="signIn">
       <input type="button" value="Sign     In" @click="signIn">
    </div>

    <div class="signUp">
       <input type="button" value="Sign     Up">
    </div>

    </form>
    <div class="losebg" v-if="loseboolean" @click="losealt">
    <div class="lose">
         <h2>登陆失败</h2>
         <p>账号 或 密码不正确!!!</p>
    </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "signIn",
  
  data() {
    return {
      accountAlt: "",  //账号格式提醒
      account:"",      //账号框字符
      password:'',     //密码
      alt:"登陆错误账号或密码错误",
      us:[],
      loseboolean:false    
    };
  },
  methods:{
    
    // 账号格式提醒
    blurAccount(){
      if(this.account.length<5){
      this.accountAlt="请输入正确账号 最少5位";
      }else{
        this.accountAlt="";
      }
    },
    //登陆验证传值
    signIn(){
      this.us={a:this.account,m:this.password}
      this.axios.post('name',this.us).then(   // 账户密码请求
         (res)=>{   // 得到返回值进行判断
           if(res.data){
                this.$router.push({path:'/home'})

           }else{
             this.loseboolean=true
           }
         
         
         }
                    ).catch(
                      (error)=>{console.log(error)})
    },
    losealt(){   //关闭账号登陆失败
             this.loseboolean=false
    }
  }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.losebg{
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.7);
  z-index: 999;
  position:fixed;
}
.lose{
     width: 4rem;
     height: 4rem;
     background:rgba(255,255,255,1);
     color: #000;
     font-weight: 400;
     border-radius:0.2rem;
     z-index: 999;
     position: absolute;
     top:50%;
     left: 50%;
     margin-top: -3rem;
     margin-left: -2rem;
}

.lose>h2{
  text-align: center;
  color: rgb(255, 35, 35);
  margin-top: 19%;
}
.lose>p{
  text-align: center;
  margin-top: 10%;
}
.sign-container{
  width: 100%;
  height: 100vh;
  background-color:#33323d;
  overflow: hidden;
}
form:first-child{
  position: absolute;
    left: 0;
    right: 0;
    max-width: 100%;
    padding: 35px 35px 15px 35px;
    margin-top: 15vh;
}
h3:first-child{
  color: #fff;
  text-align: center;
  font-size: 26px;
}
.sign-account{
  padding-right: 5px;
  max-width: 400px; 
  margin: 0 auto;
  margin-top: 30px;
  border: 1px solid #7c7c78;
  border-radius: 5px;
  overflow: hidden;
  background-color:#25242c;
}
.sign-account::before{
  content: "";
  clear: both;
}
.sign-account>input{
  outline: none;
  height: 40px;
  background:#25242c;
  width: 80%;
  color: #fff;
  border: none;
  float:left;
  font-size: 16px;
}
.sign-account>img{
  width: 20px;
  margin:9px 8px;
  float: left;
}
.sign-password{
  padding-right: 5px;
  max-width: 400px;
  margin: 0 auto;
  margin-top: 20px;
  border: 1px solid #7c7c78;
  border-radius: 5px;
  overflow: hidden;
  background-color:#25242c;
}
.sign-password::before{
  content: "";
  clear: both;
  display: block;
}
.sign-password>input{
  outline: none;
  height: 40px;
  background:#25242c;
  width: 80%;
  color: #fff;
  border: none;
  float:left;
  font-size: 16px;
}
.sign-password>img{
  width: 20px;
  margin:9px 8px;
  float: left;
}
span{
  color:#f56c6c;
  display:block;
  margin: 0 auto;
  max-width: 400px;
  min-height: 18px;
  font-size: 12px;
}
.signIn{
  margin: 0 auto;
  max-width: 400px;
  min-height: 40px;
  margin-top: 20px;
}
.signIn>input{
  width: 100%;
  min-height: 40px;
  outline: none;
  border: none;
  font-size: 16px;
  background: #409EFF;
  color: #fff;
  border-radius: 5px;
}
.signIn>input:hover{
  background: rgb(32, 132, 231);
}
.signUp{
  margin: 0 auto;
  max-width: 400px;
  min-height: 40px;
  margin-top: 20px;
}
.signUp>input{
  width: 100%;
  min-height: 40px;
  outline: none;
  border: none;
  font-size: 16px;
  background:#fff;
  color: #000;
  border-radius: 5px;
}
.signUp>input:hover{
background:rgb(221, 221, 221);
}
</style>



