<template>
  <div class="car">
    <!-- 管理 -->
    <div class="banner-bg"></div>
    <div class="header" :style="bg">
      <p>购物车</p>
      <p>管理</p>
    </div>
    <!-- 搜索 -->
    <div class="search">
        <div class="sear">
        <input type="text" name="" value="" placeholder="清空满减活动">
        </div>
    </div>
    <!-- 结算 -->
    <div class="pay">
        <div class="pay-el"><div></div></div>
        <h1>全选</h1>
                <p>结算</p>
        <em>
            <i>合计:</i>
            <span>￥{{muchs}}</span>
        </em>

    </div>
    <!-- 商品 -->
    <carx  v-for="(item,index) in wares" :key="index" :carxs="item"></carx>
    <!-- <div>{{ w.carWare }}</div> -->
    <foot :footImg="ftImg"></foot>
  </div>
</template>
<script>
import '../../assets/shop/mendian1.jpg';

export default {
  data() {
    return {
      car:'0',
     ftImg:{home:require("@/assets/footer/dp.png"),
             kind:require("../../assets/footer/md.png"),
             market:require("../../assets/footer/sp.png"),
             car:require("@/assets/footer/gwc2.png"),
             mine:require("@/assets/footer/grzx.png"),
             },

      bg: "background:rgba(58, 58, 58,0);",
    };
  },
  computed:{
      wares(){ return this.$store.state.count},
      muchs(){ return this.$store.state.prices}
  },
  
  mounted() {
    this.scroll();
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.bg = "background:rgba(58, 58, 58," + 0.01 * scrollTop + ");";
    },
    scroll(){window.scrollTo(0,0);}
  },

};
</script>
<style scoped>
.car{
  margin-bottom:2.1rem;
}
.header {
  width: 100%;
  height: 0.9rem;
  position: fixed;
  z-index: 999;
}
.header::after{
  content: '';
  display: block;
  clear: both;
}
.banner-bg {
  width: 100%;
  height: 5rem;
  position: fixed;
  top: 0rem;
  z-index: -1;
  background: url(../../assets/mine/car-ba.png) 0rem 0rem no-repeat;
  background-size: cover;
}
.header > p:nth-child(1) {
  font-size: 0.4rem;
  line-height: 0.88rem;
  color: #fff;
  float: left;
  margin-left: 0.24rem;
}
.header > p:nth-child(2) {
  font-size: 0.36rem;
  line-height: 0.88rem;
  color: #fff;
  float: right;
  margin-right: 0.24rem;
}
.search{
    padding-left: 0.24rem;
    padding-right: 0.24rem;
    padding-top: 1rem;
}
.sear{
    width: 100%;
    height: 0.72rem;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 0.1rem;
    font-size: 0.36rem;
    color: #6F6F6F;
}
.sear>input{
    padding-left: 0.2rem;
    width: 95%;
    height: 0.72rem;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 0.1rem;
    font-size: 0.36rem;
    color: #6F6F6F;
}
.pay{
    width: 100%;
    height: 1rem;
    background: #FFFFFF;
    position: fixed;
    bottom: 0.98rem;
    z-index: 999;
    border-top: #ebebeb 0.02rem solid;
    box-sizing: border-box;
}
.pay::after{
    content: "";
    clear: both;
    display: block;
}
.pay-el{
    float: left;
    height: 1rem;
    width: 0.7rem;
}
.pay-el::after{
  content: '';
  display: block;
  clear: both;
}
.pay-el>div{
    width: 0.3rem;
    height: 0.3rem;
    border: #393939 solid 0.02rem;
    border-radius: 2rem;
    float: left;
    position: relative;
    top: 50%;
    left: 0.3rem;
    margin-top: -0.16rem;
}
.pay>h1{
    height: 1rem;
    float: left;
    margin-left: 0.2rem;
    line-height: 1rem;
    font-size: 0.35rem;
    font-weight: 400;
}
.pay>em{
    font-style: normal;
    width: 2rem;
    height: 1rem;
    float: right;
    margin-right: 0.2rem;

}
.pay>em>i{
        font-style: normal;
    font-size: 0.3rem;
    font-weight: bold;
    line-height: 1rem;
}
.pay>em>span{
     margin-left: 0.1rem;
    font-style: normal;
    color: #B10002;
    font-size: 0.32rem;
    font-weight: bold;
    line-height: 1rem;
}
.pay>p{
    float: right;
    width: 1.5rem;
    height: 1rem;
    background: #B10002;
    color: #fff;
    font-size: 0.3rem;
    text-align: center;
    line-height: 1rem;
}
</style>





