<template>
  <div class="home">
    <div class="header" :style="bg">
        <div class="head">
           <img :src="homeHead.src" alt="">
        </div>
        <div class="search">
            <input type="text" name="" value="春季新款卫衣">
            <img src="../../assets/icon/search.png" alt="">
        </div>
        <div class="service">
          <img src="../../assets/icon/kf.png" alt="">
        </div>
    </div>
    <!-- banner -->
    <banner :banner="homeBan"></banner>
    <!-- 分类 -->
    <div class="home-classify">
      <div class="classify" v-for="(item,index) in classify" :key="index">
        <div>
          <div>
              <img :src="item.src" alt="">
          </div>
          <p>{{item.p}}</p>
        </div>
      </div>
    </div>
    <!-- 推荐 -->
    <div class="recommend">
      <div class="rmc-let">
        <img alt :src="recommend.big">
      </div>
      <div class="rmc-rig">
        <div class="rmc-rig-top">
          <img :src="recommend.small" alt>
        </div>
        <div class="rmc-rig-bom">
          <div class="bom-let">
            <img :src="recommend.smallLeft" alt>
          </div>
          <div class="bom-rig">
            <img :src="recommend.smallRight" alt>
          </div>
        </div>
      </div>
    </div>
    <!-- 品牌 -->
    <div class="home-brand">
      <div class="brand">
        <img src="../../assets/home/BRAND.png" alt>
      </div>
      <div class="brand-img">
        <div v-for="(item,index) in brand" :key="index">
          <img :src="item.src" alt>
        </div>
      </div>
    </div>
    <!-- 热卖 -->
    <div class="hot">
      <div class="brand">
        <img src="../../assets/home/HOT.png" alt>
      </div>
    </div>
    <!-- shop  -->
    <div class="ware-box">
    <ware v-for="(item,index) in wares" :ware="item" :key="index"></ware>
    </div>
    <foot :footImg='ftImg'></foot>
  </div>
</template>
<script>
export default {
  data() {
    return {
      wares:[],
      recommend:"",
      ftImg:{home:require("@/assets/footer/dp2.png"),
             kind:require("@/assets/footer/md.png"),
             market:require("@/assets/footer/sp.png"),
             car:require("@/assets/footer/gwc.png"),
             mine:require("@/assets/footer/grzx.png"),
             },
      homeBan:[{src:require("../../assets/banner/banner1.png")},
                 {src:require("../../assets/banner/banner2.jpg")}                
                  ],
       
      bg:"background:rgba(58, 58, 58,0);",
      homeHead:{src:require("../../assets/mine/head.jpeg")},
      brand: [   // 品牌图片
        { src: require("../../assets/home/pp-1.jpg") },
        { src: require("../../assets/home/pp-2.jpg") },
        { src: require("../../assets/home/pp-3.jpg") },
        { src: require("../../assets/home/pp-4.jpg") },
        { src: require("../../assets/home/pp-5.jpg") },
        { src: require("../../assets/home/pp-6.jpg") },
        { src: require("../../assets/home/pp-7.jpg") },
        { src: require("../../assets/home/pp-8.jpg") },
        { src: require("../../assets/home/pp-9.jpg") }
      ],
      classify: [    // 分类图片
        { src: require("../../assets/icon/n1.png"),p:"时尚女装" },
        { src: require("../../assets/icon/n2.png"),p:"新品女鞋" },
        { src: require("../../assets/icon/n3.png"),p:"品味男装" },
        { src: require("../../assets/icon/n4.png"),p:"新品男鞋" },
        { src: require("../../assets/icon/n5.png"),p:"帽子新品" },
        { src: require("../../assets/icon/n6.png"),p:"时尚女装" },
        { src: require("../../assets/icon/n7.png"),p:"新品女鞋" },
        { src: require("../../assets/icon/n8.png"),p:"品味男装" },
        { src: require("../../assets/icon/n9.png"),p:"新品男鞋" },
        { src: require("../../assets/icon/n10.png"),p:"帽子新品" },
      ],
    };
  },
  mounted(){
    window.addEventListener('scroll', this.handleScroll)  //添加监听滚动条事件
    this.waresAJX()  // 加载商品
    this.recommendAJX() // 加载每日推荐
     //this.waresAJX()
     this.scroll();

  },
  methods: {
    handleScroll(){   //监听滚动条
      var scrollTop= window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.bg="background:rgba(58, 58, 58,"+0.01*scrollTop+");"
      
    },
    waresAJX(){   // 商品请求
        this.axios.post('wares').then(  
         (res)=>{ this.wares=res.data.waresSJ; 
                  }
                    ).catch(
                      (error)=>{console.log(error)})
    },
    recommendAJX(){
      this.axios.post('recommend').then(
        (res)=>{this.recommend=res.data.recommendSJ[0]}
      ).catch(
                      (error)=>{console.log(error)}
      )
    },
        scroll(){window.scrollTo(0,0);}


  },
  computed:{
     cl(){
       return this.$store.state.count;
     }
  }
};
</script>
<style scoped>
.home{
  margin-bottom: 1.1rem;
}
.header{
  width:100%;
  height: 0.9rem;
  position:fixed;
  z-index: 999;
}
.header::after{
  content: "";
  clear: both;
  display: block;
}
.head{
    width: 0.67rem;
    height: 0.67rem;
    border: solid rgba(255,255,255,0.9) 0.03rem;
    float: left;
    border-radius: 200rem;
    margin-left: 0.24rem;
    margin-top: 0.07rem;
    overflow: hidden;
}
.head>img{
  width: 100%;
}
.search{
    float: left;
    width: 5.3rem;
    height: 0.6rem;
    margin-top: 0.12rem;
    margin-left: 0.26rem;
    border-bottom: #FFFFFF solid 0.01rem;
}
.search::after{
  content: "";
  clear: both;
  display: block;
}
.search>input{
    background: none;
    outline: none;
    color: #fff;
    width: 4.5rem;
    border: none;
    height: 0.5rem;
    font-size: 0.28rem;
    margin-top: 0.05rem;
    float: left;
}
.search>img{
    float: left;
    display: block;
    width: 0.5rem;
    margin-left: 0.25rem;
    margin-top: 0.05rem;
}
.service{
    float: right;
    margin-right: 0.24rem;
    width: 0.5rem;
    margin-top: 0.2rem;
}
.service>img{width: 100%;}
.home-classify{
  display: flex;
  display: webkit-flex;
  justify-content:space-between;
  flex-wrap: wrap;
  padding: 0rem 0.24rem;
}
.classify {
  margin-top: 0.42rem;
  width: 17%;
}
.classify > div > div {
  width: 1rem;
  height: 1rem;
  border-radius: 0.2rem;
  background: #efeeef;
  margin: 0 auto;
  overflow: hidden;
}
.classify > div > div > img {
  display: block;
  margin: 0 auto;
  width: 0.6rem;
  margin-top: 0.2rem;
}
.classify > div > p {
  font-size: 0.26rem;
  color: #313131;
  margin-top: 0.1rem;
  text-align: center;
}
.recommend {
  width: 100%;
  padding: 0rem;
  margin-top: 0.42rem;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
}
.recommend img {
  width: 100%;
  height: 100%;
}
.rmc-let {
  width: 40%;
  height: 4rem;
  border-top: #f4f4f4 0.05rem solid;
  border-right: #f4f4f4 0.05rem solid;
  border-bottom: #f4f4f4 0.05rem solid;
}
.rmc-rig {
  width: 60%;
  height: 4rem;
  border-top: #f4f4f4 0.05rem solid;
  border-bottom: #f4f4f4 0.05rem solid;
  overflow: hidden;
}
.rmc-rig-top {
  width: 100%;
  height: 50%;
  border-bottom: #f4f4f4 0.05rem solid;
}
.rmc-rig-bom {
  height: 50%;
  width: 100%;
  display: flex;
  display: -webkit-flex;
}
.bom-let {
  width: 50%;
  height: 100%;
  border-right: #f4f4f4 0.05rem solid;
}
.bom-rig {
  width: 50%;
  height: 100%;
}
.brand {
  width: 100%;
  margin-top: 0.42rem;
  height: 0.88rem;
}
.brand-img > div {
  margin-top: 0.24rem;
}
.brand > img {
  height: 100%;
}
.brand-img {
  margin-top: 0.24rem;
  padding: 0rem 0.24rem;
  display: flex;
  display: webkit-flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.brand-img > div {
  width: 2.15rem;
  height: 1.14rem;
  border: solid #f9f9f9 0.02rem;
}
.brand-img > div > img {
  display: block;
  width: 80%;
  margin: 0 auto;
  margin-top: 0.05rem;
}
.ware-box {
  padding: 0rem 0.24rem;
  margin-top: 0.24rem;
  display: flex;
  justify-content:space-between;
  flex-wrap: wrap;
}
</style>





