<template>
<div class="kind">
  <div class="header" :style="bg">
    <div class="head">
      <img :src="homeHead.src" alt>
    </div>
    <div class="search">
      <input type="text" name="" placeholder="春季新款卫衣">
      <img src="../../assets/icon/search2.png" alt>
    </div>
    <div class="service">
      <img src="../../assets/icon/kf2.png" alt>
    </div>
  </div>
  <!-- banner -->
  <banner :banner="kindBan"></banner>
  <!-- classify -->
   <classify :classBig="kindBig" :classSmall="kindSmall"></classify>
   <classify :classBig="kindBig" :classSmall="kindSmall"></classify>
   <classify :classBig="kindBig" :classSmall="kindSmall"></classify>
   <foot :footImg='ftImg'></foot>
 </div> 
</template>

<script>
export default {
  data() {
    return{
            ftImg:{home:require("@/assets/footer/dp.png"),
             kind:require("../../assets/footer/md2.png"),
             market:require("../../assets/footer/sp.png"),
             car:require("@/assets/footer/gwc.png"),
             mine:require("@/assets/footer/grzx.png"),
             },
    kindBan: [{src:require("../../assets/banner/banner2-2.jpg")},
                 {src:require("../../assets/banner/banner2-3.jpg")},                
                 {src:require("../../assets/banner/banner2-4.jpg")}               
                  ],
    kindBig:{src:require("../../assets/classify/fl-m2.png"),title:"法式优雅",
             p:"驼色的法式优雅朴素随意的中性风格尽显女性干练与洒脱"},
    kindSmall:[{src:require("../../assets/classify/fl-m1.png"),p:"织带金属圆连衣裙"},
             {src:require("../../assets/classify/fl-m2.png"),p:"织带金属圆连衣裙"},
             {src:require("../../assets/classify/fl-m3.png"),p:"织带金属圆连衣裙"}],
		bg:"background:rgba(255, 255, 255,0);",
		homeHead:{src:require("../../assets/mine/head.jpeg")},
	}
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.scroll();
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.bg = "background:rgba(255, 255, 255," + 0.01 * scrollTop + ");";
    },
        scroll(){window.scrollTo(0,0);}

  }
};
</script>

<style scoped>
.kind{
    margin-bottom: 1.1rem;
}
.header{
  width:100%;
  height: 0.9rem;
  position:fixed;
  z-index: 999;
}
.header::after{
  content: "";
  clear: both;
  display: block;
}
.head{
    width: 0.67rem;
    height: 0.67rem;
    border: solid rgba(0, 0, 0, 0.9).03rem;
    float: left;
    border-radius: 200rem;
    margin-left: 0.24rem;
    margin-top: 0.07rem;
    overflow: hidden;
}
.head>img{
  width: 100%;
}
.search{
    float: left;
    width: 5.3rem;
    height: 0.6rem;
    margin-top: 0.12rem;
    margin-left: 0.26rem;
    border-bottom: #000 solid 0.01rem;
}
.search::after{
  content: "";
  clear: both;
  display: block;
}
.search::after{
  content: "";
  clear: both;
  display: block;
}
.search>input{
    background: none;
    outline: none;
    color: #000;
    width: 4.5rem;
    border: none;
    height: 0.5rem;
    font-size: 0.28rem;
    margin-top: 0.05rem;
    float: left;
}
.search>img{
    float: left;
    display: block;
    width: 0.5rem;
    margin-left: 0.25rem;
    margin-top: 0.05rem;
}
.service{
    float: right;
    margin-right: 0.24rem;
    width: 0.5rem;
    margin-top: 0.2rem;
}
.service>img{width: 100%;}

</style>





