<template>
 <div class="footer">
    <router-link to="/home">
       <div><img :src="footImg.home"></div>
       <p>商城首页</p>
    </router-link>
    <router-link to="/kind">
       <div><img :src="footImg.kind"></div>
       <p>品牌分类</p>
    </router-link>
    <router-link to="/market">
       <div><img :src="footImg.market"></div>
       <p>品牌商城</p>
    </router-link>
    <router-link to="/car">
       <div><img :src="footImg.car"></div>
       <p>购物车</p>
    </router-link>
    <router-link to="/mine">
       <div><img :src="footImg.mine"></div>
       <p>个人中心</p>
    </router-link>
 </div>   
</template>
<script scoped>
export default {
    name: "index",
  data() {
    return {   
           
    };
  },
  props:['footImg']
}
</script>
<style>
.footer{
  width: 100%;
  height: 1.05rem;
  background: #3a3a3a;
  position: fixed;
  bottom: 0px;
  z-index: 999;
  display: flex;
  
}
.footer>a{
  display: block;
  width: 20%;
  text-decoration:none;
}
.footer>a>div{
  padding: 0.02rem;
   margin-top: 0.06rem;
}
.footer>a>div>img{
  display: block;
  margin: 0 auto;
  width: 0.6rem;
  height: 0.59rem;
}
.footer>a>p{
  font-size: 0.2rem;
  color: #fff;
  text-align: center;
  padding-bottom: 0.01rem;
}
</style>





