<template>
  <div class="carx">
    <div class="car-x">
      <div class="carx-top">
        <div class="dx"></div>
        <img src="../../assets/icon/dianpu(3).png" alt>
        <h1>{{carxs.shopName}}</h1>
        <span @click="dele">删除</span>
      </div>
      <div class="carx-bom">
        <div class="dxs"></div>
        <div class="carx-bom-img">
          <img  :src="carxs.src" alt="">
        </div>
        <p>{{carxs.describe}}</p>
        <div class="bom-much">
            <p>￥{{carxs.price}}</p>
            <div class="bom-j">
               <span @click="addNB">+</span>
               <b>{{carxs.g}}</b>
               <p @click="cutNB">-</p>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "carxs",
  data() {
    return {
    };
  },
  methods:{
    dele(){   //删除
           this.$store.commit('del',this.carxs.id)
    },
    addNB(){  // 增加
       this.$store.commit('adds',this.carxs.id)
    },
    cutNB(){  // 减少
     
       this.$store.commit('cuts',this.carxs.id)      
    }

  },
  props:['carxs'],

 };
</script>
<style scoped>
.carx{
  padding: 0 0.24rem;
}
.car-x {
  width: 100%;
  height: 4rem;
  background: #ffffff;
  border: #ebebeb solid 0.02rem;
  border-radius: 0.2rem;
  margin-top: 0.2rem;
  overflow: hidden;
}
.carx-top {
  width: 100%;
  height: 1rem;
  background: #ebebeb;
  display: inline-block;
}
.carx-top::after{
  content:"";
  clear: both;
  display: block;
}
.carx-bom {
  width: 100%;
  overflow: hidden;
}
.carx-bom::after{
  content: "";
  clear: both;
  display: block;
}
.dx {
  width: 0.3rem;
  height: 0.3rem;
  border: #393939 solid 0.02rem;
  border-radius: 2rem;
  position: relative;
  top: 50%;
  left: 0.2rem;
  background: none;
  margin-top: -0.16rem;
}
.carx-top>img{
    width: 0.5rem;
    float: left;
    margin-left: 0.8rem;
    margin-top: 0.07rem;
}
.carx-top>h1{
    width: 4rem;
    float: left;
    margin-left: 0.2rem;
    margin-top: 0.1rem;
    color: #585858;
    font-size: 0.30rem;
}
.carx-top>span{
    font-size: 0.3rem;
    font-weight: 400;
    float: left;
    margin-left: 0.4rem;
    margin-top: 0.14rem;
}
.dxs{
   width: 0.3rem;
    height: 0.3rem;
    border: #393939 solid 0.02rem;
    border-radius: 2rem;
    margin-left: 0.5rem;
    margin-top: 1.2rem;
    float: left;
}
.carx-bom-img{
    width: 2rem;
    height: 2rem;
    float: left;
    margin-top: 0.4rem;
    margin-left: 0.5rem;
}
.carx-bom-img>img{
  width: 100%;
  height: 100%;
}
.carx-bom>P{
    width: 2.7rem;
    height: 1.5rem;
    float: left;
    margin-top:0.35rem;
    margin-left: 0.2rem;
    font-size: 0.28rem;
    overflow: hidden;
    color: #787878;
    word-wrap: break-word;
}
.bom-much{
    width: 2.7rem;
    height: 0.7rem;
    float: right;
    margin-right: 0.8rem;
}
.bom-much::after{
  content: '';
  display: block;
  clear: both;
}
.bom-much>p{
    color: #B9070A;
    font-size: 0.3rem;
    font-weight: bold;
    float: left;
    line-height: 0.7rem;
}
.bom-j{
    height: 0.5rem;
    background: #FFFFFF;
    float: right;
    margin-top: 0.1rem;
    border: #f1f1f1 solid 0.01rem;
}
.bom-j::after{
  content: '';
  display: block;
  clear: both;
}
.bom-j>span{
      width: 0.4rem;
    height: 0.5rem;
    border-right: #f1f1f1 solid 0.01rem;
    color: #535353;
    text-align: center;
    line-height: 0.5rem;
    float: left;
    font-size: 0.26rem;
    display: block;
    font-weight: 800;
}
.bom-j>b{
    width: 0.8rem;
    text-align: center;
    float: left;
    height: 0.5rem;
    line-height: 0.5rem;
    font-size: 0.26rem;
    font-weight: bold;
    display: block;
}
.bom-j>p{
    width: 0.4rem;
    height: 0.5rem;
    border-left: #f1f1f1 solid 0.01rem;
    color: #535353;
    text-align: center;
    line-height: 0.5rem;
    float: right;
    font-size: 0.26rem;
    font-weight: 800;

}

</style>





