<template>
    <div class="banner">
        <ul>
            <li v-for="(item,index) in banner" :key="index">
              <img :src="item.src" alt="">
            </li>
        </ul>
    </div>


</template>
<script>
import { content } from "../../manage/banner.js";

export default {
    name: "banner",
    data(){
    return {   
      };
    },
    props:['banner'],
    mounted () {
     moun:{
       content()
     }
   } 

}
</script>
<style scoped> 
     .banner{width: 100%;height: 4.5rem;overflow: hidden;}
     .banner>ul:first-child{background: #fff;width: 200rem;height: 100%;transition: all 0.2s ease;}
     .banner>ul>li{width: 100vw;height: 100%;float: left;}
     .banner>ul>li>img{width:100%;height: 100%;}
     .banner>ul::after{
       content: "";
       display: block;
       clear: both;
     }
</style>





