<template>
<div class="shop">
 <div class="sgc">
	<div class="sgc-img"><img alt="" :src="shop.src"></div>
	<h1>{{shop.title}}</h1>
	<p><span>·</span>{{shop.content}}</p>
	<p><span>·</span>{{shop.span}}</p>
</div></div>

</template>

<script>
export default {
  name: "shop",
  data() {
    return {
    };
	},
	props:['shop']
};
</script>
<style scoped>
.sgc{
	margin-bottom: 0.2rem;
}
.sgc-img{
	width: 100%;
	height: 5rem;
}
.sgc-img>img{
	width: 100%;
	height: 100%;
}
.sgc>h1{
	margin-left: 0.24rem;
	font-size: 0.34rem;
	margin-top: 0.1rem;
}
.sgc>p{
	margin-left: 0.24rem;
	font-size: 0.28rem;
	color: #525252;
	font-weight: bolder;
}
.sgc>p>span{
	font-size: 0.40rem;
}

</style>





