<template>
<div class="ware">
      <div class="ware-let">
         <img :src="ware.src" alt="">
         <div class="ware-in">
           <p>{{ware.describe}}</p>
         </div>
          <div class="much">
             <span>￥{{ware.price}}</span>
              <img src="../../assets/icon/shoppingcar.png" alt="" @click="shopCar">
          </div> 
      </div>
</div>

</template>

<script>
export default {
  name: "ware",
  data() {
    return {
    };
  },
  props:['ware'],
  methods:{
    shopCar(){
           this.$store.commit('add',this.ware.id)
    }
  }
};
</script>
<style scoped>
.ware-let {
  width: 3.4rem;
  border: #f7f7f7 solid 0.02rem;
  margin-bottom: 0.24rem;
}
.ware-let > img {
  width: 3.4rem;
  height: 3.4rem;
  display: block;
}
.ware-in {
  font-size: 0.26rem;
  color: #000000;
  font-weight: 400;
  margin-top: 0.07rem;
}
.ware-in::after{
  content:"";
  display: block;
  clear: both;
}
.ware-in > p {
  width: 2.8rem;
  margin-left: 0.1rem;
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ware-in > img {
  width: 0.35rem;
  margin-left: 0.05rem;
  margin-top: 0.02rem;
}
.much {
  margin-bottom: 0.08rem;
}
.much::after{
  content: "";
  display: block;
  clear: both;
}
.much > span {
  width: 3.4rem;
  color: #930906;
  font-weight: bolder;
  margin-left: 0.08rem;
}
.much > img {
  width: 0.45rem;
  margin-left: 0.05rem;
  margin-top: 0.02rem;
  float: right;
  margin-right: 0.12rem;
  display: block;
}
</style>





