// 引入mockjs
const Mock = require('mockjs');

const wares=require('./mockjs/wares')  // 引入商品数据
const recommend=require('./mockjs/recommend ')  // 引入商品数据
const users=require('./mockjs/user')  // 引入用户账号


var list=function(options){  // 账号密码验证
    var user=JSON.parse(JSON.stringify(users)).users; //账号密码库
    var us=JSON.parse(options.body);  //前端账号密码
    for(let i in user){
        if(us.a==user[i].account&&us.m==user[i].password){
            return true
        }
    }
    
    }


Mock.mock('name',list)  //拦截登陆请求
Mock.mock('wares',wares)  //拦截商品请求
Mock.mock('recommend',recommend)  //拦截首页 推荐






