<template>
<div class="classify">
<div class="fl"><img src="../../assets/classify/fl1.jpg" alt=""><div class="fl-more">更多</div></div>	
<div class="fl-i">
     <img alt="" :src="classBig.src">
	 <div class="fl-t">
	     <h1>{{classBig.title}}</h1>
		 <p>{{classBig.p}}</p>
		 <div class="fl-m">查看详情</div>
	 </div>
</div>
<div class="fl-s">
     <div class="fl-s-m" v-for="(item,index) in classSmall" :key="index">
	    <img alt="" :src="item.src">
		 <p>{{item.p}}</p>
	 </div>
</div>
</div>
    
</template>
<script>
export default {
    name:"classify",
    data(){
        return{
        name:"classify",
    }
	},
	props:['classBig','classSmall']
}
</script>
<style scoped>

.fl{
	width: 100%;
	height: 1.5rem;
}
.fl::after{
	content: "";
	display: block;
	clear: both;
}
.fl>img{
	width: 100%;
	height: 100%;
}
.fl-more{
	width: 1rem;
	font-size: 0.30rem;
	color: #fff;
	border-left: #fff solid 0.05rem;
	position: relative;
	top:-1rem;
	left: 6.4rem;
	text-align: center;
}
.fl-i{
	padding: 0rem 0.48rem;
}
.fl-i::after{
	content: "";
	display: block;
	clear: both;
}
.fl-i>img{
	width: 3.7rem;
	display: block;
	height: 4.9rem;
	float: left;
	margin-top: 0.3rem;
}
.fl-t{
	float: left;
}
.fl-t::after{
	content: '';
	clear: both;
	display: block;
}
.fl-t>h1{
	font-size: 0.34rem;
	margin-top: 1.7rem;
	margin-left: 0.24rem;
}
.fl-t>p{
	margin-left: 0.24rem;
	width: 2.5rem;
	height: 2rem;
	font-size: 0.26rem;
	color: #626262;
	line-height: 0.4rem;
}
.fl-m{
	width: 1.5rem;
	height: 0.5rem;
	background: #000000;
	color: #FFFFFF;
	font-size: 0.30rem;
	text-align: center;
	line-height: 0.5rem;
	margin-left: 1rem;
}
.fl-s{
	display: flex;
	padding: 0.3rem 0.24rem;
	clear: both;
	justify-content: space-between;
}
.fl-s-m>img{
	width: 2.2rem;
	height: 2.7rem;
}
.fl-s-m>p{
	width: 1.9rem;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	font-size: 0.26rem;
	color: #939393;
}
</style>





