// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// 引入ui组件
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
//引入mockjs
import './mock.js'
//引入axios模块
import axios from "axios";
Vue.prototype.axios = axios;
//引入Vuex
import store from './store/locus'

//注册组件
import banner from "./components/group/banner"
Vue.component("banner",banner)
import carx from "./components/group/carx"
Vue.component("carx",carx)
import classify from "./components/group/classify"
Vue.component("classify",classify)
import shop from "./components/group/shop"
Vue.component("shop",shop)
import ware from "./components/group/ware"
Vue.component("ware",ware)

import foot from './components/group/foot'
Vue.component('foot',foot)

Vue.config.productionTip = false;


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  
})



