import Vue from 'vue';
import Vuex from 'vuex';

const wares=require('../mockjs/wares')  // 引入商品数据

Vue.use(Vuex)
export default new Vuex.Store({
state:{
     count:[],          //商品
     prices:0            // 总价格
    
    },
mutations:{  // 更改 Vuex 的store 中的状态唯一方法是提交 mutation
    
       del(state,describeID){   //删除商品
           for(var i in state.count){
            if(state.count[i].id==describeID){
                state.prices=state.prices-parseFloat(state.count[i].g)*parseFloat(state.count[i].price)
                state.count.splice(i,1)
            }
        }
           },
        add(state,addware){    //购物车添加
        const x=JSON.parse(JSON.stringify(wares)).waresSJ
         var vs=1;
               for(var k in state.count){
                   if(state.count[k].id==addware){
                      state.count[k].g++;
                      vs=0;
                      state.prices=state.prices+parseFloat(state.count[k].price)         
                   break;
                   }else{ vs=1 }
                };
               if(vs==1){
                for(var i in x ){
                        if(x[i].id==addware){
                            state.count.push(x[i])
                            var m=state.count.length-1
                            state.prices=state.prices+(parseFloat(state.count[m].g)*parseFloat(state.count[m].price))
                            }                 
                        }
            
                   
                };
           },

        adds (state,addID){   // 添加时候计算
          for(let i in state.count){
              if(state.count[i].id==addID){
                state.count[i].g++;
                state.prices=state.prices+parseFloat(state.count[i].price)
              }
          }
             },
        cuts( state,cutID){   //减号
            for(let i in state.count){
                if(state.count[i].id==cutID){
                    if(state.count[i].g<=1){
                        state.count[i].g=1
                    }else{
                        state.count[i].g--
                        state.prices=state.prices-parseFloat(state.count[i].price)

                    }
                }
            }
          },
    } 
        
    
})



